<script>
    import axios from 'axios'

    let API_URL = 'https://desolate-beach-96822.herokuapp.com/'
    export default {
        name: "ConnectionMixin",
        data: function() {
            return {
                token:null
            }
        },
        methods:
        {
            getToken()
            {
                return this.$store.state.tokenStore.token
            },
            getUserId()
            {
                return this.$store.state.tokenStore.userId
            },
            connect(connectionToken, userId)
            {
                this.$store.commit('connect',{connectionToken,userId})
            },
            disconnect()
            {
                this.$store.commit('disconnect')
            },
            getAuthenticatedRoute()
            {
                if(this.isConnected)
                {
                    let axiosInstance = axios.create({
                        baseURL: API_URL,
                        headers: {
                            'Content-Type': 'application/json',
                            'Authorization': 'Bearer '+this.getToken()
                        }
                    })

                    return axiosInstance
                }
                else
                {
                    throw Error('Erro: Usurio no autenticado')
                }
            },
            getUnauthenticatedRoute()
            {
                let axiosInstance = axios.create({
                    baseURL: API_URL,
                    headers: {
                        'Content-Type': 'application/json'
                    }
                })
                return axiosInstance
            },
            //Funo dedicada para lidar com erros na comunicao com o BackEnd
            //Entrada: Erro detalhado encontrado na requisio
            //Tarefa: Tomar medidas necessrias para impedir que esse erro interrompa o funcionamento do site e alertar
            //o usurio sobre o erro.
            //V&V: Identificao da origem do erro, erro interno do servidor ou falha na comunicao. Em caso de erro
            // interno identificao do cdigo do erro. O cdigo 401 significa uma falha de autenticao, nesse caso o
            // usurio  desconectado, alertado e redirecionado para a pgina principal. Caso seja outro cdigo o usurio
            //  alertado da mensagem do erro. Caso seja um erro na comunicao o usurio  alertado da falha.
            //Ps Condies: Funcionamento do site sem interrupo, em caso de erro de autenticao a desconexo e redire-
            //cionamento para a pgina principal devem ser feitos. O usurio sempre deve ser alertado quanto ao erro.
            async handleResponseError(error)
            {
                if(error.response)
                {
                    if(error.response.status === 401)
                    {
                        this.disconnect()
                        await this.$alert('Conexo expirada. Faa login novamente.')
                        await this.$router.push('/')
                        return
                    }
                    await this.$alert(error.response.data.message)
                } else {
                    await this.$alert(error)
                }

            }
        },
        computed:
        {
            isConnected()
            {
                return this.$store.getters.isConnected
            }
        }
    }
</script>
